import { IndexStatus } from "@rp/common/utils"
import { axiosClient } from "@rp/react/axios-client"
import { FILES_BASE_URL, FILES_ROOT } from "../file.constants"

const getIndexStatus = async (id: string): Promise<IndexStatus> => {
  const { data } = await axiosClient.get<IndexStatus>(
    `${FILES_BASE_URL}${FILES_ROOT}/${id}/index-status`,
  )

  return data
}

export { getIndexStatus }
